@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.two-col-section{
    padding: 40px;
    background-color: #F1F2F3;
}

.two-col-heading{
    font-weight: bold;
    font-size: 28px;
    margin-bottom: 1.5rem;
    text-align: center;
}

.two-col-para{
    text-align: center;
    
}

.two-col-para::after{
    content: '';
    background: #FF7A00;
    display: block;
    height: 3px;
    width: 170px;
    margin:15px auto 5px;
    margin-bottom: 40px;
}

.two-col-image{
    width: 100%;
}

.col-two{
    background-color: #FFFFFF;
    padding: 20px;
    border-radius: 5px;
}