@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.payroll-bg{
    background-image: url("./cal-2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 600px;
    opacity: 1 #5D3511;
   
}

.empty-div{
    margin-bottom: 80px;
   font-size: 0px;
}

.payroll-heading{
    color: #5D3511;
   margin-left: 100px;
  font-weight: bold;
  margin-bottom: 20px;
}

.payroll-para{
    color: #5D3511;
   margin-left: 100px;
  font-weight: bold;
  
}

.payroll-para::after{
    content: '';
    background: #5D3511;
    display: block;
    height: 3px;
    width: 170px;
    margin:15px 0px;
   
}

.payroll-heading-two{
    color: #5D3511;
    margin-left: 100px;
   font-weight: bold;
   margin-bottom: 20px;
}

.payroll-ul{
    margin-left: 80px;
}

.payroll-li{
    color: #5D3511;
    margin-bottom: 5px;
    
}

.payroll-btn{
    background-color:#5D3511;
    padding: 15px;
    color: white;
    margin-left: 100px;
}

