@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}



.header-logo{
    height: 100px;
    margin-left: 15px;
    margin-bottom: 30px;
}

.banner-bg{
    background-image: url("./banner.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 650px;
}

.banner-ul{
    float: right;
    margin-top: 30px;
    
}

.banner-li{
display: inline;
margin-right: 45px;

}

.banner-a{
    color: white;
    text-decoration: none;
    font-size: 18px;
}

.banner-a:hover{
    color: #ED7302;
}

.banner-curve{
    background-color: #D6D6D4;
    height: 650px;
    width: 55%;
    border-radius: 0px 500px 0px 0px;
    opacity: 0.8;
}

.heading-left{
    margin-left: 80px;
    color: black;
    margin-bottom: 25px;
    font-weight: bolder;
    font-size: 25px;
    
}

.heading-center{
    margin-left: 80px;
    font-weight: bolder;
    font-size: 45px;
    color: black;
    margin-bottom: 25px;
}

.banner-para{
    color: #000000;
    margin-left: 80px;
    font-size: 20px;
    margin-bottom: 25px;
}

.bg-ul{
    margin-left: 80px;
}

.bg-li{
    margin-bottom: 20px;
    color: #000000;
    font-size: 20px;
}