@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

#four-col-section{
    padding: 40px;
    background-color: rgb(241,242,243);
}

.four-flex{
    display: flex;
    flex-direction: row;
}

.four-one{
    color: #051c2c;
    background: #fff;
    padding: 10px;
    box-shadow: 0 0 20px 0 rgb(0 17 81 / 20%);
    border-radius: 8px;
   width: 28%;
   height: 300px;
   margin-right: 20px;
   margin-left: 50px;
   margin-bottom: 20px;
}

