#blod{
    position: relative;
    left: 8px;
}

.four-para{
    margin-top: 25px;
    margin-left: 10px;
}

.four-one:hover #blod::after{
    content: '';
    background: blue;
    display: block;
    height: 3px;
    width: 150px;
    margin-right: auto;
    margin-top: 10px;
}

.four-one:hover{
