@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.three-col-heading{
    font-weight: bold;
    font-size: 28px;
    margin-bottom: 1.5rem;
    text-align: center;
}

.three-col-section{
    padding: 40px;
   
}

.three-col-para{
    text-align: center;
    
}

.three-col-para::after{
    content: '';
    background: #FF7A00;
    display: block;
    height: 3px;
    width: 170px;
    margin:15px auto 5px;
    margin-bottom: 40px;
}

.flex{
    position: relative;
}

.box-2{
   position: absolute;
   left: 850px;
   top: 140px;
  color: #051c2c;
    background: #fff;
    box-shadow: 0 0 20px 0 rgb(0 17 81 / 20%);
    border-radius: 8px;
    width: 30%;
    height: 230px;
  
}

.box-3{
    position: absolute;
    left: 450px;
    top: 120px;
    color: #051c2c;
    background: #fff;
    padding: 30px;
    box-shadow: 0 0 20px 0 rgb(0 17 81 / 20%);
    border-radius: 8px;
    width: 30%;
    height: 300px;
    
}

.box-1{
    position: relative;
    left: 40px;
    color: #051c2c;
    background: #fff;
   width: 30%;
   height: 300px;
    box-shadow: 0 0 20px 0 rgb(0 17 81 / 20%);
    border-radius: 8px;
    margin-bottom: 30px;
    padding: 30px;
}

.box-1:hover{
    background-color: #182862;
    color: white;
}

.box-3:hover{
    background-color: #182862;
    color: white;
}

.box-4:hover{
    background-color: #182862;
    color: white;
}

.box-4{
    position: relative;
    left: 40px;
    color: #051c2c;
    background: #fff;
    padding: 30px;
    box-shadow: 0 0 20px 0 rgb(0 17 81 / 20%);
    border-radius: 8px;
    width: 30%;
    height: 250px;
}

.three-images{
    width: 100%;
}


