@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

#sbs-section{
    padding: 40px;
    background-color: rgb(241,242,243);
    margin-bottom: 30px;
}

.sbs-flex{
    display: flex;
}

.sbs-one{
    background-color: #fff;
    width: 40%;
    margin-right: 30px;
    margin-left: 15px;
    padding: 20px;
}

.sbs-two{
    background-color: #fff;
    width: 40%;
    margin-right: 10px;
    margin-right: 30px;
    padding: 20px;
}

.sbs-three{
    background-color: #fff;
    width: 40%;
    margin-right: 15px;
    margin-right: 30px;
    padding: 20px;
}

#sbs-heading{
    margin-bottom: 30px;
}

.sbs-one:hover #sbs-heading::after{
    content: '';
    background: blue;
    display: block;
    height: 3px;
    width: 150px;
    margin-right: auto;
    margin-top: 20px;
}

.sbs-one:hover{
    border-bottom: 4px solid blue;
    color: blue;
}

.sbs-two:hover #sbs-heading::after{
    content: '';
    background: blue;
    display: block;
    height: 3px;
    width: 150px;
    margin-right: auto;
    margin-top: 20px;
}

.sbs-two:hover{
    border-bottom: 4px solid blue;
    color: blue;
}

.sbs-three:hover #sbs-heading::after{
    content: '';
    background: blue;
    display: block;
    height: 3px;
    width: 150px;
    margin-right: auto;
    margin-top: 20px;
}

.sbs-three:hover{
    border-bottom: 4px solid blue;
    color: blue;
}