@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}



.three-col-second{
    padding: 40px;
}

.sec-flex{
    position: relative;
}

.sec-1:hover{
    background-color: #182862;
    color: white;
}

.sec-3:hover{
    background-color: #182862;
    color: white;
}

.sec-4:hover{
    background-color: #182862;
    color: white;
}


.sec-1{
    color: #051c2c;
    background: #fff;
    width: 30%;
    height: 300px;
    box-shadow: 0 0 20px 0 rgb(0 17 81 / 20%);
    border-radius: 8px;
    padding: 30px;
    margin-left: auto;
    margin-bottom: 30px;
    position: relative;
    right: 40px; 
}
