@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.form-section{
   padding: 60px;
}

.form-flex{
display: flex;
}

.form-two{
    margin-left: 100px;
    margin-top: 70px;
    width: 50%;
}

.form-input{
    width: 100%;
   border: none;
    outline: none;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
    margin-bottom: 45px;
}

.form-label{
    color: #333;
    font-weight: bold;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
}

.form-two-flex{
    display: flex;
    justify-content: space-between;
   
}

.form-btn{
    background-color: #FF7A00;
    padding: 10px;
    color: #fff;
    border: none;
    border-radius: 5px;
}